import React,{ useState }from 'react';

 const Helloo = () => {
 	const [Result,setResult] = useState("");

 	const handleClick =(e)=>{
 		setResult(Result.concat(e.target.name));

 	}
 	const clear = () =>{
 	setResult("");}
const backspace = () => {
 setResult(Result.slice(0,-1));
}
const calculate = () =>{
try{	
setResult(eval(Result).toString());}

	catch(err){
		setResult("error");
	}



  
}


 	return (
 		<div><h1>CALCULATOR</h1>
 		<div className="calculator">

           <form>
           
           <input type="text"  class="display" value={Result} />
           </form>
           <div className="keypads">
           <div className="row">
           <button onClick={clear} id="clear" >AC</button> 
           <button onClick={backspace} id="backspace">c</button></div>
           <div className="row">
           
           <button name="1" onClick={handleClick}>1</button> 
           <button name="2" onClick={handleClick}>2</button> 
            
           <button name="3" onClick={handleClick}>3</button> 
           <button name="4"
           onClick={handleClick}>4</button> </div>
           <div className="row">
           <button name="5" onClick={handleClick}>5</button> 
           <button name="6" onClick={handleClick}>6</button> 
           <button name="7" onClick={handleClick}>7</button> 
           <button name="8" onClick={handleClick}>8</button> </div>
           <div className="row">
           <button name="9" onClick={handleClick}>9</button> 
           <button name="0" onClick={handleClick}>0</button>
           <button name="." onClick={handleClick}>.</button> 
           <button name="+" onClick={handleClick} className="operator">+</button> </div>
           <div className="row">
           <button name="-" onClick={handleClick} className="operator">-</button> 
           <button name="*" onClick={handleClick} className="operator">*</button>
           <button name="/" onClick={handleClick} className="operator">/</button>
           <button name="=" onClick={calculate} className="operator">=</button> </div>   
 		</div></div></div>
 		);}
 	
 	

 export default Helloo;